/**
 * The following styles get applied both on the front of your site
 * and in the editor.
 *
 * Replace them with your own styles or remove the file completely.
 */
.wp-block-miscellaneous-gutenberg-blocks-search {
	&.show-search-icon {
		button {
			position: relative;
			&:before {
				content: "";
				display: inline-block;
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background-color: black;
				mask-image: url('data:image/svg+xml, <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="lucide lucide-search-icon lucide-search" ><path d="m21 21-4.34-4.34" /><circle cx="11" cy="11" r="8" /></svg>');
				mask-repeat: no-repeat;
				mask-position: center;
			}
		}
	}
	&.has-style {
		display: flex;
		justify-content: space-between;
		align-items: center;
		position: relative;
		height: 56px;
		background: transparent;
		border-radius: 56px;
		overflow: hidden;
		border: solid 3px #eee;
		* {
			&:focus {
				outline: none;
			}
		}
		select,
		input[type="search"],
		button[type="submit"] {
			border-radius: 0;
			background-color: transparent;
			border: none;
			font-family: inherit;
			font-size: inherit;
			font-weight: inherit;
			color: inherit;
		}
		select.search-category {
			padding: 8px 25px 8px 25px;
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;
			background-color: transparent;
			background-color: #eee;
			width: 50%;
			height: 100%;
			cursor: pointer;
		}
		input[type="search"].search-input {
			padding: 8px 0 8px 25px;
			padding-right: 0;
			width: 50%;
		}
		button[type="submit"].search-button {
			display: flex;
			align-items: center;
			justify-content: center;
			flex-shrink: 0;
			border-radius: 56px;
			padding: 8px 25px 8px 25px;
			svg {
				color: #333;
				width: 14px;
				height: 14px;
			}
		}

		&.show-category.show-search-icon {
			button[type="submit"].search-button {
				width: 56px;
				padding: 0;
				aspect-ratio: 1;
			}
			select.search-category {
				width: calc((100% - 56px) / 2);
			}
			input[type="search"].search-input {
				width: calc((100% - 56px) / 2);
			}
		}
	}
}
